.list-item-button {
  position: relative;
  color: inherit;
  text-decoration: none;
}

.list-item-button::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 10%;
  border-radius: 10px;
  background-color: #FCAD0D;
  transition: width 0.3s ease;
  z-index: 1;
}

.list-item-button:hover::before,
.list-item-button.active::before {
  width: 100%;
  background-color: #FCAD0D;
  
}





.custom-button1:hover {
  /* Remove the hover effect by setting the same styles as the default state */
  background-color:  #FCAD0D  !important; /* Set the background color to the default color */
  /* Add any other styles you want to reset on hover here */
}

.custom-button2:hover {
  /* Remove the hover effect by setting the same styles as the default state */
  background-color: white !important; /* Set the background color to the default color */
  /* Add any other styles you want to reset on hover here */
}




@media (min-width: 600px) {
  .list-item-button:hover::before {
    bottom: -0.25rem;
    left: 0;
    width: 100%;
    height: 10%;
    background-color: #FCAD0D;
    content: "";
    position: absolute;
  }

  .list-item-button.active {
    &::before {
      bottom: -0.25rem;
      left: 0;
      width: 100%;
      height: 10%;
      background-color: #FCAD0D;
      content: "";
      position: absolute;
    }
  }
}

